
#app {
  position: absolute;
  top: 0;
  bottom: 0;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  background: #fff;
}
html {
  font-family: "PingFangSC-Regular", "Helvetica Neue", Helvetica, Arial;
  font-size: 20px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
body {
  font-size: 0.7rem;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  color: #333;
  background: #fff;
}
textarea:focus,
input:focus,
label:focus,
div:focus {
  border: none;
  outline: 0;
}
textarea {
  resize: none;
  padding: 0;
  border: none;
}
body,
ul,
p,
dl,
dd {
  margin: 0;
  padding: 0;
}
a {
  -webkit-tap-highlight-color: rgba(255,0,0,0);
}
a:focus {
  background: none;
}
li {
  -webkit-tap-highlight-color: rgba(255,0,0,0);
  list-style-type: none;
}
li:focus {
  background: none;
}
img[lazy=loading] {
  width: 100%;
}
.c-wp {
  height: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
}
.c-att-ul li {
  position: relative;
  height: 3rem;
  padding: 0.75rem;
  border-bottom: 1px solid #eee;
  overflow: hidden;
}
.c-att-ul img {
  float: left;
  box-sizing: border-box;
  width: 3rem;
  height: 3rem;
  margin-right: 0.75rem;
  border: 1px solid rgba(238,238,238,0.3);
  border-radius: 50%;
  background-size: 100% 100%;
  background-clip: padding-box;
  overflow: hidden;
}
.c-att-ul .c-att-des {
  height: 100%;
}
.c-att-ul .c-att-title {
  margin: 0;
  margin-right: 3.8rem;
  color: #333;
  font-size: 0.85rem;
  font-weight: 400;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.c-att-ul .c-att-fans {
  line-height: 0.85rem;
  color: #c3c3c3;
  font-size: 0.6rem;
}
.c-att-ul .c-att-info {
  margin-right: 4.2rem;
  color: #999;
  font-size: 0.6rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.c-tab-list {
  background: #fff;
}
.c-tab-list .c-tab-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 2.2rem;
  line-height: 2.2rem;
  font-size: 0.8rem;
  color: #999;
  border-bottom: 1px solid #f6f6f6;
}
.c-tab-list .c-tab-title > li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.c-tab-list .c-tab-title > li.on {
  position: relative;
  color: #333;
}
.c-tab-list .c-tab-title > li.on:after {
  position: absolute;
  bottom: 0;
  content: '';
  left: 50%;
  margin-left: -10px;
  width: 20px;
  height: 1px;
  background: #333;
}
.c-tab-ul > li {
  position: relative;
  padding: 0.75rem 0.75rem 0;
  overflow: hidden;
}
.c-tab-ul > li:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #f6f6f6;
}
.c-media-info {
  display: inline-block;
}
.c-media-info > img {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
  vertical-align: -0.6rem;
  border: 1px solid rgba(238,238,238,0.3);
  border-radius: 50%;
  background-color: #fff;
  background-size: 100% 100%;
  background-clip: padding-box;
  overflow: hidden;
}
.c-media-info > p {
  display: inline-block;
  white-space: nowrap;
  max-width: 210px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
  vertical-align: middle;
}
.c-att-t {
  position: absolute;
  right: 0.75rem;
  top: 0.9rem;
  width: 2.5rem;
  height: 1.25rem;
  line-height: 23px;
  text-align: center;
  font-size: 10px;
  color: #2873ff;
  border: 1px solid #2873ff;
  text-decoration: none;
  box-sizing: border-box;
}
.c-att-t.on {
  border: 1px solid #eee;
  color: #ccc;
}
.c-att-t > span {
  vertical-align: 1px;
}
.c-att-delete {
  position: absolute;
  right: 0.8rem;
  top: 1.1rem;
  font-size: 0.6rem;
  text-decoration: none;
  background: url("./assets/delete.png") center no-repeat;
  height: 1rem;
  width: 1rem;
  background-size: 20px auto;
  padding: 2px;
}
.c-att-delete.on {
  border: 1px solid #eee;
  color: #ccc;
}
.c-media-desc {
  margin: 11px 0 12px;
  font-size: 0.85rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.c-media-qing {
  -webkit-line-clamp: 5;
}
.c-media-content img {
  width: 100%;
  background: url("./assets/default.jpg") center no-repeat;
  background-size: 100% 100%;
}
.c-media-long {
  overflow: hidden;
  margin: 7px 0 12px;
}
.c-media-long > p {
  float: left;
  overflow: hidden;
  width: 66.7%;
  height: 46px;
  line-height: 23px;
  font-size: 0.85rem;
  padding-right: 20px;
  box-sizing: border-box;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.c-media-long > img {
  float: right;
  width: 33.3%;
  margin-top: 3px;
}
.c-media-oper {
  clear: both;
  height: 20px;
  margin-top: 8px;
  margin-bottom: 0.6rem;
  font-size: 12px;
  color: #c3c3c3;
  line-height: 20px;
}
.c-media-oper > p {
  float: left;
}
.c-media-qing-more {
  position: relative;
  overflow: hidden;
  width: 100%;
  margin-bottom: 12px;
}
.c-media-qing-more img {
  float: left;
  width: 33.3%;
  height: auto;
  padding-right: 5px;
  box-sizing: border-box;
  max-height: 100%;
}
.c-media-qing-more img:last-of-type {
  padding-right: 0;
}
.c-media-qing-more .c-qing-num {
  position: absolute;
  right: 4px;
  bottom: 4px;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  font-size: 0.5rem;
  background: rgba(27,27,27,0.8);
  border-radius: 1px;
  color: #fff;
}
.c-media-video {
  position: relative;
  overflow: hidden;
}
.c-media-video .media-video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  background: rgba(0,0,0,0.3);
  border-radius: 100px;
  border: 1px solid transparent;
}
.c-media-video .media-video-btn:after {
  content: '';
  position: absolute;
  top: 16px;
  left: 18px;
  width: 0;
  height: 0;
  border-top: 9px solid transparent;
  border-left: 16px solid #fff;
  border-bottom: 9px solid transparent;
}
.c-media-video .c-media-time {
  position: absolute;
  background: rgba(27,27,27,0.4);
  border-radius: 2px;
  color: #fff;
  line-height: 1;
  bottom: 0.4rem;
  right: 0.3rem;
  font-size: 10px;
  border-radius: 2px;
  width: 34px;
  height: 16px;
  line-height: 16px;
  text-align: center;
}
.c-media-audio {
  overflow: hidden;
  margin-bottom: 12px;
  margin-top: 11px;
  font-size: 0.85rem;
}
.c-media-audio .c-media-desc {
  margin-right: 10px;
  word-break: break-all;
}
.c-media-audio .media-audio-pic {
  position: relative;
  width: 3.5rem;
  height: 3.5rem;
  float: right;
  margin-right: 0.9rem;
  margin-left: 1.5rem;
  border-radius: 50%;
  border: 2px solid #eee;
}
.c-media-audio .media-audio-pic:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 97;
  background: #1b1b1b;
  opacity: 0.3;
  border-radius: 50%;
}
.c-media-audio .media-audio-pic:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 100;
  width: 0;
  height: 0;
  border-top: 9px solid transparent;
  border-left: 16px solid #fff;
  border-bottom: 9px solid transparent;
}
.c-media-audio .media-audio-pic > img {
  background: url("./assets/audio_default.png") center no-repeat;
  background-size: 100% 100%;
  display: block;
  width: 100%;
  min-height: 100%;
  border-radius: 50%;
}
.c-empty {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: -72px;
}
.c-empty p {
  width: 80px;
  height: 80px;
  text-align: center;
}
.c-empty p img {
  width: 100%;
}
@-webkit-keyframes rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.c-loading {
  background: #fff;
  text-align: center;
  font-size: 0.6rem;
  padding: 0.5rem 0;
}
.c-loading p {
  line-height: 1.8;
}
.c-loading .loading-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url("./assets/loading.png");
  background-size: auto 18px;
  -webkit-animation: rotate 0.8s steps(12) infinite;
          animation: rotate 0.8s steps(12) infinite;
}


