
.c-media-oper .c-tab-ue {
  float: right;
  margin-right: 0.75rem;
  height: 20px;
}
.c-media-oper .c-tab-ue > span {
  position: relative;
  z-index: 15;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-left: 20px;
}
.c-media-oper .c-zan .zan-icon {
  display: inline-block;
  height: 20px;
  width: 20px;
  background: url("../assets/zan.png") center no-repeat;
  background-size: 100% auto;
}
.c-media-oper .c-zan .on {
  background: url("../assets/zan.png") center no-repeat;
  background-size: 100% auto;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-animation: zans 800ms linear 1 forwards;
          animation: zans 800ms linear 1 forwards;
}
.c-media-oper .c-zan .on-no-inmation {
  background: url("../assets/tag-zan.png") center no-repeat;
  background-size: 100% auto;
}
.c-media-oper .c-zan .c-add1 {
  position: absolute;
  right: -13px;
  z-index: 99;
  color: #4c92fb;
  -webkit-animation: add 800ms linear 1 forwards;
          animation: add 800ms linear 1 forwards;
}
.c-media-oper .c-num {
  position: absolute;
  top: -5px;
  left: 17px;
  line-height: 1;
  color: #4c92fb;
  font-size: 0.5rem;
}
.c-media-oper .c-comment {
  background: url("../assets/comment.png") center no-repeat;
  background-size: 100% auto;
}
.c-media-oper .c-comment .c-num {
  padding: 2px;
  top: -7px;
  left: 14px;
  background: #fff;
}
@-webkit-keyframes add {
0% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    opacity: 0;
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px);
}
}
@keyframes add {
0% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
100% {
    opacity: 0;
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px);
}
}
@-webkit-keyframes zans {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes zans {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}


